import Component from '../../core/Component';
import { vendingMachine } from '../../domains/VendingMachine';
import { CHARGE_AMOUNT } from '../../configs/constants';

class ChangeChargePage extends Component {
  template() {
    const coins = vendingMachine.useStore((state) => state.coins);
    const coinArray = [...Object.entries(coins)].sort(([a], [b]) => b - a);
    const totalMoney = vendingMachine.getTotalMoney();

    return `
      <section>
        <h2 hidden>ÏûîÎèà Ï∂©Ï†Ñ</h2>
        <form id="change-charge-form" class="change-charge-form">
          <div>
            <label for="amount" class="description">ÏûêÌåêÍ∏∞Í∞Ä Î≥¥Ïú†Ìï† Í∏àÏï°ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.</label>
            <input
              id="charge-amount"
              class="charge-amount-input styled-input"
              name="amount"
              placeholder="Í∏àÏï°"
              type="number"
              min="${CHARGE_AMOUNT.MIN}"
              max="${CHARGE_AMOUNT.MAX}"
              step="${CHARGE_AMOUNT.STEP}"
              required
              autofocus
            >
          </div>
          <button class="add-charge-button styled-button emphasized">Ï∂©Ï†Ñ</button>
        </form>
        <p class="current-money-indicator">ÌòÑÏû¨ Î≥¥Ïú† Í∏àÏï°: <span>${totalMoney}Ïõê</span></p>
      </section>
      <section>
        <h2 class="table-title">ÏûêÌåêÍ∏∞Í∞Ä Î≥¥Ïú†Ìïú ÎèôÏ†Ñ</h2>
        <table class="styled-table">
          <thead>
            <tr class="styled-tr">
              <th class="styled-th">ÎèôÏ†Ñ</th>
              <th class="styled-th">Í∞úÏàò</th>
            </tr>
          </thead>
          <tbody>
            ${coinArray
              .map(
                ([key, value]) => `
                  <tr class="styled-tr">
                    <td class="styled-td">${key}Ïõê</td>
                    <td class="styled-td">${value}Í∞ú</td>
                  </tr>
                `
              )
              .join('')}
          </tbody>
        </table>
      </section>
    `;
  }

  setEvent() {
    this.addEvent('submit', '#change-charge-form', (event) => {
      event.preventDefault();

      const chargeAmount = this.querySelector('#charge-amount').valueAsNumber;

      try {
        vendingMachine.addCoin(chargeAmount);
      } catch (err) {
        window.alert(err);
      }
    });
  }
}

customElements.define('change-charge', ChangeChargePage);
